<template>
  <va-card :title="$t('tables.labelDeliveryWorkReport')+' '+ nameDelivery" class="w-100">

    <va-data-table
      :fields="fields"
      :data="dataTable"
      :loading="loading"
      api-mode
      no-pagination
    >
      <template slot="marker" slot-scope="props">
        {{props.rowIndex + 1}}
      </template>
      <template slot="inv" slot-scope="props">
        {{ props.rowData.inv.name == null ? '---':props.rowData.inv.name}}
      </template>
      <template slot="delivery" slot-scope="props">
        {{ props.rowData.delivery.name== null ? '---':props.rowData.delivery.name}}
      </template>
      <template slot="tr_Stock_Type" slot-scope="props">
        {{ props.rowData.tr_Stock_Type.name == null ? '---':props.rowData.tr_Stock_Type.name}}
      </template>
      <template slot="status" slot-scope="props">
        <va-badge v-if="props.rowData.is_Deliverd" :color="'success'">
          {{ $t('tables.is_delivered') }}
        </va-badge>
        <va-badge v-else-if="!props.rowData.is_Deliverd" :color="'danger'">
          {{ $t('tables.is_failed ') }}
        </va-badge>

      </template>
      <template slot="actions" slot-scope="props">
        <va-button flat small color="info" @click="showViewModal(props.rowData)" class="ma-0">
          {{ $t('tables.view') }}
        </va-button>
      </template>
    </va-data-table>
    <va-modal v-model="ViewModal"
      size="large"
      :title=" $t('generic.restrictionsDetailes') "
      class="viewNodel"
      hide-default-actions >
      <div class="row">
        <va-data-table style="width: 100%;"
          :fields="tr_Sub_fields"
          :data="currentobjectView.tr_Stock_Subs"
          api-mode
          no-pagination
        >
          <template slot="marker" slot-scope="props">
            {{props.rowIndex + 1}}
          </template>
          <template slot="products" slot-scope="props">
            {{ props.rowData.products.name == null ? '---':props.rowData.products.name}}
          </template>
          <template slot="stock" slot-scope="props">
            {{ props.rowData.stock.name == null ? '---':props.rowData.stock.name}}
          </template>
          <template slot="units" slot-scope="props">
            {{ props.rowData.units.name == null ? '---':props.rowData.units.name}}
          </template>
          <template slot="status" slot-scope="props">
            <va-badge v-if="props.rowData.is_Deliverd" :color="'success'">
              {{ $t('tables.is_delivered') }}
            </va-badge>
            <va-badge v-else-if="!props.rowData.is_Deliverd" :color="'danger'">
              {{ $t('tables.is_failed ') }}
            </va-badge>

          </template>
        </va-data-table>

      </div>
    </va-modal>
  </va-card>
</template>

<script>
import BillService from '../../../../services/BIll/BillService'
export default {
  props: {
    idDelivery: Number,
    nameDelivery: Number,
  },
  data () {
    return {
      modelTitle: '',
      modelOktext: '',
      ViewModal: false,
      search: {},
      dataTable: [],
      loading: true,
      currentobjectView: {},
    }
  },

  computed: {
    fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
        title: '#',
      }, {
        name: '__slot:inv',
        title: this.$t('tables.headings.inv_id'),
      },
      {
        name: '__slot:delivery',
        title: this.$t('tables.headings.deliveryName'),
      },
      {
        name: '__slot:tr_Stock_Type',
        title: this.$t('tables.headings.tr_Stock_Type'),
      },
      {
        name: '__slot:status',
        title: this.$t('tables.headings.status'),
      },
      {
        name: '__slot:actions',
        dataClass: 'text-right',
      }]
    },

    tr_Sub_fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
        title: '#',
      }, {
        name: '__slot:products',
        title: this.$t('tables.headings.product'),
      },
      {
        name: '__slot:units',
        title: this.$t('tables.headings.units'),
      },
      {
        name: '__slot:stock',
        title: this.$t('tables.headings.stock'),
      },
      {
        name: 'quantity',
        title: this.$t('tables.headings.quantity'),
      },
      {
        name: '__slot:status',
        title: this.$t('tables.headings.status'),
      },
      {
        name: '__slot:actions',
        dataClass: 'text-right',
      }]
    },
  },
  created () {
    this.readItems()
  },
  methods: {
    readItems (isDeliverd = null) {
      this.loading = true
      const params = {
        delivery_id: this.idDelivery === 0 ? null : this.idDelivery,
        Is_Deliverd: isDeliverd,

      }
      BillService.getAllForDelivery(params)
        .then(response => {
          console.log(response)
          this.dataTable = response.data
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    showViewModal (cuobject) {
      this.ViewModal = true
      this.currentobjectView = cuobject
    },
  },
}

</script>

<style lang="scss">
  i.fa.fa-exclamation {
    color: red !important;
  }

</style>
